.header {
	position: relative;
	height: 95vh;
	background: linear-gradient(90.05deg, rgba(186, 133, 84, 0.7) 0.03%, rgba(1, 1, 1, 0.7) 99.94%), url(../IMG/ballons_backgroung.jpg) center / cover no-repeat;
	clip-path: polygon(0 0,100% 0,100% 100%,0 80%);
	&__logo {
		position: absolute;
		width: 5.6rem;
		height: 7rem;
		top: 5rem;
		left: 5rem; }
	&__box {
		position: absolute;
		top: 40%;
		left: 50%;
		transform: translate(-50%,-50%);
		width: 100%;
		text-align: center; } }
